export default {
	data() {
		return {
			status: [{
					key: 1,
					name: '待发货',
					url: '../../static/fumou-center-template/one.png',
					link: 'waitingGods'
				},
				{
					key: 2,
					name: '待收货',
					url: '../../static/fumou-center-template/2.png',
					link: 'waitingGet'
				},
				{
					key: 3,
					name: '待评价',
					url: '../../static/fumou-center-template/3.png',
					link: 'waitingCommand'
				},
				{
					key: 4,
					name: '全部订单',
					url: '../../static/fumou-center-template/4.png',
					link: 'allOrder'
				}
			],
		};
	},
	methods: {
		// 进入地址管理
		gotoAddress() {
			uni.navigateTo({
				url: '../chb-address/address-list/address-list',
			});
		},
		// 点击跳转订单状态列表
		gotoDetail(e) {
			var status = e
			switch (status) {
				case 'waitingGods':
					uni.navigateTo({
						url: 'waitingGods',
					});
					break
				case 'waitingGet':
					uni.navigateTo({
						url: 'waitingGods',
					});
					break
				case 'waitingCommand':
					uni.navigateTo({
						url: 'waitingGods',
					});
					break
				case 'allOrder':
					uni.navigateTo({
						url: 'waitingGods',
					});
					break

			}
		},
		// 跳转意见反馈
		gotoFeedBack() {
			uni.navigateTo({
				url: 'uni-feedback',
			});
		},
		// 帮助中心。拨打电话
		callCenter(){
			uni.makePhoneCall({
				phoneNumber:'15215084164'
			})
		},
		// 跳转个人收藏
		gotoSaveGoods() {
			uni.navigateTo({
				url: 'saveGoods',
			});
		},
		// 跳转关于我们
		gotoAboutUs() {
			uni.navigateTo({
				url: 'aboutMine',
			});
		}
	},
	computed: {

	}
